@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");

body {
  margin: 0;
  padding: 20px;
  position: absolute;
  width: 100vw;
}

.container {
  margin-left: 20px;
  margin-top: 20px;
}

.card {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
  width: 100%;
}

.pokemon {
  border: 1px solid black;
  background-color: aquamarine;
  height: 15rem;
}

img {
  margin-left: 3px;
  margin-top: 3px;
  padding-left: 3px;
  padding-top: 3px;
  height: 80px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: aqua;
}

h1 {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: 24px;
  margin: auto;
  width: 50%;
  padding: 10px;
}

@media (max-width: 320px) {
  .card {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
    position: relative;
  }
}

.type {
  display: none;
}

.form {
  text-align: center;
  align-items: center;
  left: 50%;
  padding-top: 1rem;
  transform: translate(-50%);
  position: relative;
}

.btn {
  display: none;
}
